@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.sprite {
  display: none;
}
.button {
  width: 260px;
  padding-top: 14px;
  padding-bottom: 14px;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  background-color: #ffffff;
  border: 2px solid #000000;
  text-decoration: none;
  color: #000000;
}
.button:hover,
.button:focus {
  color: #ffffff;
  background-color: #444444;
  border-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
  border-color: #444444;
}
.header {
  width: 100%;
}
.header__wrapper {
  width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.header__container {
  position: relative;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #ececec;
}
.header__logo-mobile {
  margin-top: 22px;
  margin-bottom: 18px;
}
a[href].header__logo-mobile:hover,
a[href].header__logo-mobile:focus {
  opacity: 0.6;
}
a[href].header__logo-mobile:active {
  opacity: 0.3;
}
.header__button-call-nav {
  position: absolute;
  top: 26px;
  right: 29px;
  display: none;
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .header__button-call-nav--open {
    display: block;
    background-image: url("../img/icon-menu-open.svg");
    background-repeat: no-repeat;
    background-size: 20px 14px;
    background-position: center;
  }
  .header__button-call-nav--close {
    display: block;
    background-image: url("../img/icon-menu-close.svg");
    background-repeat: no-repeat;
    background-size: 21px 20px;
    background-position: center;
  }
}
@media (min-width: 768px) {
  .header__wrapper {
    width: 768px;
    margin: 0 auto;
    flex-direction: row;
  }
  .header__logo-mobile {
    display: none;
  }
  .header__button-main-nav {
    display: none;
  }
}
@media (min-width: 1150px) {
  .header__wrapper {
    width: 1150px;
    margin: 0 auto;
  }
}
.navigation {
  z-index: 5;
  padding: 0;
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.navigation__item {
  position: relative;
  border-bottom: 1px solid #ececec;
}
.navigation__item--logo {
  display: none;
}
.navigation__item--delivery {
  display: none;
}
.navigation__item--new {
  display: none;
}
.navigation__item--sale {
  display: none;
}
.navigation__link {
  text-decoration: none;
  color: #000000;
  display: block;
  padding-top: 27px;
  padding-bottom: 27px;
  padding-left: 76px;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
}
a[href].navigation__link:hover,
a[href].navigation__link:focus {
  color: rgba(0, 0, 0, 0.6);
}
a[href].navigation__link:hover .navigation__link-icon,
a[href].navigation__link:focus .navigation__link-icon {
  fill: #46c1ae;
}
a[href].navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}
a[href].navigation__link:active .navigation__link-icon {
  fill: #46c1ae;
  opacity: 0.3;
}
a[href].navigation__link--logo:hover,
a[href].navigation__link--logo:focus {
  opacity: 0.6;
}
a[href].navigation__link--logo:active {
  opacity: 0.3;
}
.navigation__link-icon {
  position: absolute;
  top: 34px;
  left: 32px;
  fill: #d3d2d2;
}
@media (max-width: 767px) {
  .navigation--close {
    display: none;
  }
  .navigation--open {
    display: block;
  }
}
