.product-week__wrapper {
  width: 320px;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 27px;
}
.product-week__wrapper::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 320px;
  height: 18px;
  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: no-repeat;
  background-size: 320px 10px;
  background-position: center;
  transform: rotate(180deg);
}
.product-week__header {
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  color: #63d1bb;
  text-transform: uppercase;
}
.product-week__name {
  margin: 0;
  margin-bottom: 6px;
  padding: 0;
  font-size: 25px;
  line-height: 30px;
  font-weight: bold;
  color: #000000;
  text-transform: none;
}
.product-week__description {
  margin: 0;
  padding-bottom: 15px;
  font-size: 15px;
  line-height: 24px;
  font-weight: regular;
}
.product-week__feature-list {
  margin-bottom: 30px;
  border: none;
  border-collapse: collapse;
}
.product-week__feature-value {
  padding-left: 15px;
}
.product-week__feature-item,
.product-week__feature-value {
  padding-top: 10px;
  padding-bottom: 9px;
  font-size: 15px;
  line-height: 20px;
  font-weight: regular;
  text-align: left;
  border-bottom: 1px solid #ececec;
}
.product-week__price {
  display: inline-block;
  padding-top: 20px;
  padding-bottom: 25px;
  font-size: 17px;
