    padding-left: 0;
  }
}
.process-creation {
  width: 320px;
  margin: 0 auto;
  position: relative;
  padding-left: 30px;
  padding-right: 30px;
}
.process-creation__wrapper {
  display: flex;
  flex-direction: column;
  border: 1px solid #ececec;
  transition: 0.4s;
}
.process-creation__wrapper:hover,
.process-creation__wrapper:focus {
  box-shadow: 0 23px 25px -12px rgba(0, 0, 0, 0.2);
}
.process-creation__wrapper:active {
  box-shadow: 0 10px 25px -12px rgba(0, 0, 0, 0.2);
}
.process-creation__description {
  display: flex;
  flex-direction: column;
}
.process-creation__header {
  position: relative;
  padding-top: 25px;
  padding-left: 90px;
  padding-right: 30px;
  padding-bottom: 172px;
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
}
.process-creation__header::before {
  content: "";
  position: absolute;
  top: 35px;
  left: 32px;
  width: 32px;
  height: 22px;
  background-image: url("../img/icon-video.svg");
  background-repeat: no-repeat;
  background-size: 32px 32px;
  background-position: center;
}
.process-creation__video {
  position: absolute;
  top: 98px;
  left: 30px;
}
.process-creation__icon-play {
  position: absolute;
  top: 35px;
  left: 80px;
  z-index: 5;
  cursor: pointer;
}
.process-creation__icon-play:hover,
.process-creation__icon-play:focus {
  transform: scale(1.2);
  transition-duration: 0.5s;
}
.process-creation__icon-play:active {
  transform: scale(0.8);
  transition-duration: 0.5s;
  opacity: 0.3;
}
.process-creation__text {
  margin-top: 28px;
  margin-bottom: 25px;
  padding-left: 34px;
  padding-right: 40px;
  font-size: 13px;
  line-height: 24px;
  font-weight: regular;
}
.process-creation__button-order {
  align-self: center;
  width: 192px;
  padding-top: 16px;
  padding-bottom: 16px;
  margin-bottom: 32px;
  font-size: 17px;
  line-height: 16px;
  font-weight: bold;
}
